/* metro-ui.foundation includes all styles but the component */
/**
 * Settings provide general overarching settings split up in various specialzied partials
 *
 * @author christian.nies@uxi.de
 */
/**
 * _functions.scss partial
 *
 * Contains global functions.
 */
/**
 * Importing _family
 *
 * Family.scss is a set of Sass mixins which will help you to manage the style of :nth-child"ified elements, in an easy
 * way.
 *
 * @see https://lukyvj.github.io/family.scss/
 */
/**
 * _tools.scss partial
 *
 * Contains global mixins and functions.
 *
 * @author christian.nies@uxi.de
 */
/**
// Standard
.radius-small {
	border-radius:2px;
}
.radius-medium {
	border-radius:4px;
}
.radius-large {
	border-radius:16px;
}
 */
/**
 * _generic.scss partial
 *
 * Contains general imports for global usaged. First layer to output CSS
 *
 * @author christian.nies@uxi.de
 */
/* apply a natural box layout model to all elements, but allowing components to change */
/* @see https://www.paulirish.com/2012/box-sizing-border-box-ftw/ */
html {
  box-sizing: border-box;
  font-size: 16px;
  -ms-text-size-adjust: 100%;
  /* 3 */
  -webkit-text-size-adjust: 100%;
  /* 3 */
  -webkit-tap-highlight-color: transparent;
}

html.no-select {
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Safari */
  -khtml-user-select: none;
  /* Konqueror HTML */
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
  /* Non-prefixed version, currently supported by Chrome and Opera */
}

@media (max-width: 320px) {
  html {
    font-size: 14px;
  }
}

/**
 * Apply appropriate box sizing
 */
*,
*:before,
*:after {
  box-sizing: inherit;
}

/**
 * Importing helpers
 */
/**
 * helpers includes generic helpers for global use
 */
.centered {
  text-align: center;
  margin-right: auto;
  margin-left: auto;
}

.mobile-only {
  display: none;
  visibility: hidden;
}

@media (min-width: 321px) {
  .mobile-only {
    display: block;
    visibility: visible;
  }
}

.desktop-only {
  display: block;
  visibility: visible;
}

@media (max-width: 768px) {
  .desktop-only {
    display: none !important;
    visibility: hidden !important;
  }
}

.nowrap {
  white-space: nowrap;
}

.alignRight {
  margin-left: auto;
}

.centeredHV {
  display: flex;
  align-items: center;
}

.line-through {
  text-decoration: line-through;
}

.underline {
  text-decoration: underline;
}

